import { useState, useCallback } from 'react';
import { ethers } from 'ethers';
import { CONTRACTS, LAUNCHPAD_ABI, NFT_COLLECTION_ABI } from '../config/chains';

export function useLaunchpad(signer, provider) {
    const [collections, setCollections] = useState([]);
    const [userCollections, setUserCollections] = useState([]);
    const [isLoading, setIsLoading] = useState(false);
    const [isCreating, setIsCreating] = useState(false);
    const [error, setError] = useState(null);
    const [txHash, setTxHash] = useState(null);

    // Get launchpad contract instance
    const getLaunchpadContract = useCallback((signerOrProvider) => {
        if (!signerOrProvider || CONTRACTS.LAUNCHPAD === '0x0000000000000000000000000000000000000000') {
            return null;
        }
        return new ethers.Contract(CONTRACTS.LAUNCHPAD, LAUNCHPAD_ABI, signerOrProvider);
    }, []);

    // Fetch all collections
    const fetchAllCollections = useCallback(async () => {
        if (!provider) return;

        setIsLoading(true);
        setError(null);

        try {
            const contract = getLaunchpadContract(provider);
            if (!contract) {
                // Return demo collections if contract not deployed
                setCollections(getDemoCollections());
                return;
            }

            const allCollections = await contract.getAllCollections();

            // Fetch additional data for each collection
            const enrichedCollections = await Promise.all(
                allCollections.map(async (col, index) => {
                    try {
                        const nftContract = new ethers.Contract(col.contractAddress, NFT_COLLECTION_ABI, provider);
                        const totalMinted = await nftContract.totalMinted();

                        return {
                            id: index,
                            contractAddress: col.contractAddress,
                            name: col.name,
                            symbol: col.symbol,
                            maxSupply: Number(col.maxSupply),
                            mintPrice: ethers.formatUnits(col.mintPrice, 6),
                            creator: col.creator,
                            createdAt: Number(col.createdAt),
                            totalMinted: Number(totalMinted)
                        };
                    } catch (err) {
                        console.error('Error fetching collection data:', err);
                        return {
                            id: index,
                            contractAddress: col.contractAddress,
                            name: col.name,
                            symbol: col.symbol,
                            maxSupply: Number(col.maxSupply),
                            mintPrice: ethers.formatUnits(col.mintPrice, 6),
                            creator: col.creator,
                            createdAt: Number(col.createdAt),
                            totalMinted: 0
                        };
                    }
                })
            );

            setCollections(enrichedCollections);
        } catch (err) {
            console.error('Error fetching collections:', err);
            setError('Failed to fetch collections');
            setCollections(getDemoCollections());
        } finally {
            setIsLoading(false);
        }
    }, [provider, getLaunchpadContract]);

    // Fetch collections by creator
    const fetchUserCollections = useCallback(async (creatorAddress) => {
        if (!provider || !creatorAddress) return;

        setIsLoading(true);

        try {
            const contract = getLaunchpadContract(provider);
            if (!contract) {
                setUserCollections([]);
                return;
            }

            const collectionIds = await contract.getCollectionsByCreator(creatorAddress);
            const userCols = await Promise.all(
                collectionIds.map(async (id) => {
                    const col = await contract.getCollection(id);
                    const nftContract = new ethers.Contract(col.contractAddress, NFT_COLLECTION_ABI, provider);
                    const totalMinted = await nftContract.totalMinted();

                    return {
                        id: Number(id),
                        contractAddress: col.contractAddress,
                        name: col.name,
                        symbol: col.symbol,
                        maxSupply: Number(col.maxSupply),
                        mintPrice: ethers.formatUnits(col.mintPrice, 6),
                        creator: col.creator,
                        createdAt: Number(col.createdAt),
                        totalMinted: Number(totalMinted)
                    };
                })
            );

            setUserCollections(userCols);
        } catch (err) {
            console.error('Error fetching user collections:', err);
            setUserCollections([]);
        } finally {
            setIsLoading(false);
        }
    }, [provider, getLaunchpadContract]);

    // Create new collection
    const createCollection = useCallback(async (name, symbol, maxSupply, mintPrice, baseTokenURI) => {
        if (!signer) {
            setError('Please connect your wallet');
            return null;
        }

        setIsCreating(true);
        setError(null);
        setTxHash(null);

        try {
            const contract = getLaunchpadContract(signer);
            if (!contract) {
                setError('Launchpad contract not deployed');
                return null;
            }

            // Convert mint price to wei (USDC has 6 decimals)
            const mintPriceWei = ethers.parseUnits(mintPrice.toString(), 6);

            const tx = await contract.createCollection(
                name,
                symbol,
                maxSupply,
                mintPriceWei,
                baseTokenURI
            );

            setTxHash(tx.hash);

            const receipt = await tx.wait();

            // Get the new collection address from event
            const event = receipt.logs.find(log => {
                try {
                    const parsed = contract.interface.parseLog(log);
                    return parsed?.name === 'CollectionCreated';
                } catch {
                    return false;
                }
            });

            if (event) {
                const parsed = contract.interface.parseLog(event);
                return parsed.args.contractAddress;
            }

            return receipt.contractAddress;
        } catch (err) {
            console.error('Error creating collection:', err);
            setError(err.message || 'Failed to create collection');
            return null;
        } finally {
            setIsCreating(false);
        }
    }, [signer, getLaunchpadContract]);

    // Get single collection details
    const getCollectionDetails = useCallback(async (collectionId) => {
        if (!provider) return null;

        try {
            const contract = getLaunchpadContract(provider);
            if (!contract) {
                // Return demo collection
                const demos = getDemoCollections();
                return demos[collectionId] || demos[0];
            }

            const col = await contract.getCollection(collectionId);
            const nftContract = new ethers.Contract(col.contractAddress, NFT_COLLECTION_ABI, provider);
            const totalMinted = await nftContract.totalMinted();
            const mintingEnabled = await nftContract.mintingEnabled();

            return {
                id: collectionId,
                contractAddress: col.contractAddress,
                name: col.name,
                symbol: col.symbol,
                maxSupply: Number(col.maxSupply),
                mintPrice: ethers.formatUnits(col.mintPrice, 6),
                creator: col.creator,
                createdAt: Number(col.createdAt),
                totalMinted: Number(totalMinted),
                mintingEnabled
            };
        } catch (err) {
            console.error('Error fetching collection details:', err);
            return null;
        }
    }, [provider, getLaunchpadContract]);

    return {
        collections,
        userCollections,
        isLoading,
        isCreating,
        error,
        txHash,
        fetchAllCollections,
        fetchUserCollections,
        createCollection,
        getCollectionDetails
    };
}

// Demo collections for when contract not deployed
function getDemoCollections() {
    return [
        {
            id: 0,
            contractAddress: '0x0000000000000000000000000000000000000001',
            name: 'Cosmic Explorers',
            symbol: 'COSMIC',
            maxSupply: 1000,
            mintPrice: '0.05',
            creator: '0x742d35Cc6634C0532925a3b844Bc9e7595f0aB42',
            createdAt: Date.now() / 1000,
            totalMinted: 342
        },
        {
            id: 1,
            contractAddress: '0x0000000000000000000000000000000000000002',
            name: 'Neon Dreamers',
            symbol: 'NEON',
            maxSupply: 500,
            mintPrice: '0.08',
            creator: '0x8ba1f109551bD432803012645Ac136ddd64DBA72',
            createdAt: Date.now() / 1000 - 86400,
            totalMinted: 127
        },
        {
            id: 2,
            contractAddress: '0x0000000000000000000000000000000000000003',
            name: 'Pixel Warriors',
            symbol: 'PIXEL',
            maxSupply: 2000,
            mintPrice: '0.03',
            creator: '0xAb5801a7D398351b8bE11C439e05C5B3259aeC9B',
            createdAt: Date.now() / 1000 - 172800,
            totalMinted: 891
        }
    ];
}
