import React from 'react';
import { Link, useLocation } from 'react-router-dom';

export function Header({ account, isConnecting, isOnArcTestnet, onConnect, onDisconnect, formatAddress }) {
    const location = useLocation();

    const isActive = (path) => {
        if (path === '/') return location.pathname === '/';
        return location.pathname.startsWith(path);
    };

    return (
        <header className="header">
            <div className="container header-content">
                <Link to="/" className="logo">
                    <img src="/logo.png" alt="ArcGenesis" className="logo-img" />
                    <span className="logo-text">ArcGenesis</span>
                </Link>

                <nav className="nav">
                    <Link to="/" className={`nav-link ${isActive('/') ? 'active' : ''}`}>
                        Home
                    </Link>
                    <Link to="/create" className={`nav-link ${isActive('/create') ? 'active' : ''}`}>
                        Create
                    </Link>
                    <Link to="/my-collections" className={`nav-link ${isActive('/my-collections') ? 'active' : ''}`}>
                        My Collections
                    </Link>
                </nav>

                <div className="header-actions">
                    {account && (
                        <div className={`network-badge ${isOnArcTestnet ? '' : 'wrong'}`}>
                            <span className="network-dot"></span>
                            {isOnArcTestnet ? 'Arc Testnet' : 'Wrong Network'}
                        </div>
                    )}

                    {account ? (
                        <button
                            className="btn btn-wallet"
                            onClick={onDisconnect}
                        >
                            <span className="wallet-icon"></span>
                            {formatAddress(account)}
                        </button>
                    ) : (
                        <button
                            className="btn btn-primary"
                            onClick={onConnect}
                            disabled={isConnecting}
                        >
                            {isConnecting ? (
                                <>
                                    <span className="spinner"></span>
                                    Connecting...
                                </>
                            ) : (
                                'Connect Wallet'
                            )}
                        </button>
                    )}
                </div>
            </div>
        </header>
    );
}
