import React, { useEffect } from 'react';
import { HeroSection } from '../components/HeroSection';
import { CollectionGrid } from '../components/CollectionGrid';

export function HomePage({ collections, isLoading, fetchCollections, formatAddress }) {
    useEffect(() => {
        fetchCollections();
    }, [fetchCollections]);

    return (
        <div className="home-page">
            <HeroSection />

            <section id="collections" className="collections-section">
                <div className="container">
                    <div className="section-header">
                        <h2 className="section-title">
                            <span className="title-icon"></span>
                            Live Collections
                        </h2>
                        <p className="section-subtitle">
                            Discover and mint from the latest NFT collections on Arc Network
                        </p>
                    </div>

                    <CollectionGrid
                        collections={collections}
                        isLoading={isLoading}
                        formatAddress={formatAddress}
                    />
                </div>
            </section>

            <section className="features-section">
                <div className="container">
                    <div className="section-header">
                        <h2 className="section-title">Why ArcGenesis?</h2>
                    </div>
                    <div className="features-grid">
                        <div className="feature-card">
                            <div className="feature-icon"></div>
                            <h3>Lightning Fast</h3>
                            <p>Arc Network delivers sub-second transaction finality</p>
                        </div>
                        <div className="feature-card">
                            <div className="feature-icon"></div>
                            <h3>Low Gas Fees</h3>
                            <p>Pay gas with USDC - predictable and affordable</p>
                        </div>
                        <div className="feature-card">
                            <div className="feature-icon"></div>
                            <h3>No Code Required</h3>
                            <p>Launch your collection in minutes without coding</p>
                        </div>
                        <div className="feature-card">
                            <div className="feature-icon"></div>
                            <h3>Fully Decentralized</h3>
                            <p>Your collection, your rules - on-chain forever</p>
                        </div>
                    </div>
                </div>
            </section>
        </div>
    );
}
