import React from 'react';
import { Routes, Route } from 'react-router-dom';
import { useWallet } from './hooks/useWallet';
import { useLaunchpad } from './hooks/useLaunchpad';
import { Header } from './components/Header';
import { Footer } from './components/Footer';
import { HomePage } from './pages/HomePage';
import { CreateCollection } from './pages/CreateCollection';
import { CollectionDetail } from './pages/CollectionDetail';
import { MyCollections } from './pages/MyCollections';

function App() {
    const {
        account,
        provider,
        signer,
        isConnecting,
        isOnArcTestnet,
        connect,
        disconnect,
        switchToArcTestnet,
        formatAddress
    } = useWallet();

    const {
        collections,
        userCollections,
        isLoading,
        isCreating,
        error,
        txHash,
        fetchAllCollections,
        fetchUserCollections,
        createCollection,
        getCollectionDetails
    } = useLaunchpad(signer, provider);

    return (
        <div className="app">
            <Header
                account={account}
                isConnecting={isConnecting}
                isOnArcTestnet={isOnArcTestnet}
                onConnect={connect}
                onDisconnect={disconnect}
                formatAddress={formatAddress}
            />

            <main className="main">
                <Routes>
                    <Route
                        path="/"
                        element={
                            <HomePage
                                collections={collections}
                                isLoading={isLoading}
                                fetchCollections={fetchAllCollections}
                                formatAddress={formatAddress}
                            />
                        }
                    />
                    <Route
                        path="/create"
                        element={
                            <CreateCollection
                                account={account}
                                isOnArcTestnet={isOnArcTestnet}
                                onConnect={connect}
                                onSwitchNetwork={switchToArcTestnet}
                                createCollection={createCollection}
                                isCreating={isCreating}
                                error={error}
                                txHash={txHash}
                            />
                        }
                    />
                    <Route
                        path="/collection/:id"
                        element={
                            <CollectionDetail
                                account={account}
                                signer={signer}
                                provider={provider}
                                isOnArcTestnet={isOnArcTestnet}
                                onConnect={connect}
                                onSwitchNetwork={switchToArcTestnet}
                                getCollectionDetails={getCollectionDetails}
                                formatAddress={formatAddress}
                            />
                        }
                    />
                    <Route
                        path="/my-collections"
                        element={
                            <MyCollections
                                account={account}
                                userCollections={userCollections}
                                isLoading={isLoading}
                                fetchUserCollections={fetchUserCollections}
                                onConnect={connect}
                                formatAddress={formatAddress}
                            />
                        }
                    />
                </Routes>
            </main>

            <Footer />
        </div>
    );
}

export default App;
